import { Modal, Form, Input, Select, Radio, Spin } from 'antd';
import * as React from 'react';
import { formItemLayout } from '@/constants';
import { FormComponentProps } from 'antd/lib/form';
import { IUserListParam, IUserList, ITeamAddParam } from '@/api';
import { toastformError } from '@/utils';

import { IAction } from '@/types';

import debounce from 'lodash/debounce';

const { Option } = Select;
interface Props extends FormComponentProps {
  visible: boolean;
  onClose: (param: boolean) => void;
  userList: IUserList;
  onSearchUser: (param: IUserListParam) => IAction;
  onSubmit: (param: ITeamAddParam) => IAction;
}

const TeamAddModel = (props: Props) => {
  const { getFieldDecorator } = props.form;
  React.useEffect(() => {
    props.form.resetFields();
    //
    if (props.visible && !props.userList.list.length) {
      props.onSearchUser({ page: 1, pageSize: 50 });
    }
  }, [props.visible]);
  const handleSubmit = (e: React.MouseEvent) => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      if (err) {
        toastformError(err);
        return;
      }
      props.onSubmit(values);
      props.onClose(false);
    });
  };

  const handleSearch = debounce((value: string) => {
    props.onSearchUser({ nickname: value, page: 1, pageSize: 50 });
  }, 800);
  return (
    <Modal onOk={handleSubmit} title='' visible={props.visible} onCancel={() => props.onClose(false)}>
      <Form layout='horizontal' {...formItemLayout}>
        <Form.Item label=''>
          {getFieldDecorator('name', {
            rules: [{ required: true, message: '' }]
          })(<Input placeholder='' />)}
        </Form.Item>

        <Form.Item label=''>
          {getFieldDecorator('public', {
            initialValue: true
          })(
            <Radio.Group>
              <Radio value={true}></Radio>
              <Radio value={false}></Radio>
            </Radio.Group>
          )}
        </Form.Item>
        <Form.Item label=''>
          {getFieldDecorator('description', {})(<Input placeholder='' />)}
        </Form.Item>
        <Form.Item label=''>
          {getFieldDecorator(
            'members',
            {}
          )(
            <Select
              mode='multiple'
              onSearch={handleSearch}
              filterOption={false}
              notFoundContent={null}
              placeholder=''
            >
              {props.userList.list.map(item => (
                <Option key={item.id} value={item.id}>
                  {item.nickname || item.username}
                </Option>
              ))}
            </Select>
          )}
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default Form.create<Props>()(TeamAddModel);
